/* eslint-disable no-undef */
import data from '../data/data.json';

const menuBtns = document.querySelectorAll('.menu__tabs-btn');
const menuList = document.querySelector('.menu__list');
const menuBtnAdd = document.querySelector('.menu__btn-add');

let currentItems = [];
let currentCategory = 'coffee';
let lastWindowWidth;

if (window.innerWidth > 1320) {
  menuBtnAdd.classList.remove('menu__btn-add--visible');
}

const render = (items) => {
  menuList.innerHTML = '';
  items.forEach((item, index) => {
    const listItem = document.createElement('li');
    listItem.classList.add('menu__list-item');
    listItem.innerHTML = `
    <article class="menu__card">
      <h2 class="visually-hidden">card</h2>
      <div class="menu__card-top">
        <img src="../img/jpg/${currentCategory}-${index + 1}.jpg" alt="${item.name}">
      </div>
      <div class="menu__card-bottom">
        <strong class="menu__card-title">${item.name}</strong>
        <p class="menu__card-descr">${item.description}</p>
        <span class="menu__card-price">$${item.price}</span>
      </div>
    </article>
    `;
    menuList.append(listItem);
    if (window.innerWidth < 1320) {
      // eslint-disable-next-line no-unused-expressions
      menuList.children.length > 4 ? menuBtnAdd.classList.add('menu__btn-add--visible') : menuBtnAdd.classList.remove('menu__btn-add--visible');
      for (let i = 4; i < menuList.children.length; i += 1) {
        menuList.children[i].classList.add('menu__list-item--hidden');
      }
    }
  });
};

window.addEventListener('resize', () => {
  if (window.innerWidth !== lastWindowWidth) {
    if (window.innerWidth < 1320) {
      // eslint-disable-next-line no-unused-expressions
      menuList.children.length > 4 ? menuBtnAdd.classList.add('menu__btn-add--visible') : menuBtnAdd.classList.remove('menu__btn-add--visible');
      for (let i = 4; i < menuList.children.length; i += 1) {
        menuList.children[i].classList.add('menu__list-item--hidden');
      }
    } else {
      menuBtnAdd.classList.remove('menu__btn-add--visible');
      for (let i = 0; i < menuList.children.length; i += 1) {
        menuList.children[i].classList.remove('menu__list-item--hidden');
      }
    }
  }
  lastWindowWidth = window.innerWidth;
});

menuBtnAdd.addEventListener('click', () => {
  menuBtnAdd.classList.remove('menu__btn-add--visible');
  for (let i = 0; i < menuList.children.length; i += 1) {
    menuList.children[i].classList.remove('menu__list-item--hidden');
  }
});
// eslint-disable-next-line max-len, no-return-assign
const getCurrentItems = () => currentItems = data.filter((item) => item.category === currentCategory);

menuBtns.forEach((btn) => {
  btn.addEventListener('click', () => {
    menuBtns.forEach((currentBtn) => currentBtn.classList.remove('menu__tabs-btn--active'));
    btn.classList.add('menu__tabs-btn--active');
    currentCategory = btn.innerText.replace(/\p{Emoji}/gu, '').trim().toLowerCase();
    getCurrentItems();
    render(currentItems);
  });
});

currentItems = getCurrentItems();

render(currentItems);
